
import React, { useState } from 'react'
import Box from '@mui/material/Box';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
//import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
//import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import Getactivities from '../getmethods/getactivitesforteamleader'
import GetRewardsofteamleader from '../getmethods/getrewardsofteamleader'
import PeopleIcon from '@material-ui/icons/People';
import PersonIcon from '@material-ui/icons/Person';
import ErorList from '../errorlist'
import configData from "../config.json";
import XMLParser from 'react-xml-parser';

function Row(props) {
  const { row } = props;
  const [open, setOpen] = React.useState(false);
  const [openteamleader, setOpenteamleader] = React.useState(false);
  const [a, setA] = React.useState(false);
  async function getBonusRewards(credentials) {
    
   return fetch(configData.server+'getBonusRewards', {
     method: 'POST',
     headers: {
       'Content-Type': 'application/json'
     },
     body: JSON.stringify(credentials)
   })
     .then(data => data.json())
  
  }

  const handleSubmit = async e => {
    console.log("onclick")
    console.log(openteamleader,"openteamleader")
    setOpenteamleader(!openteamleader)
    console.log(openteamleader,"openteamleader")
    if (open)
    setOpen(!open)
              console.log(open,"opennnnnnnnnnnnnnn")
    e.preventDefault();
    let token=null
    let teamleaderid= row.attributes.CustID1
  
    try{  token = await getBonusRewards({
      teamleaderid,
    });}catch(error){
     console.log("error catch1111",error)
      token=error
    } 
    
if(token.BonusRewards.includes('Error')||token.BonusRewards.includes('GetBonusDoc - Bonus document not found')){
setA(token.BonusRewards)
}
else{
var xml = new XMLParser().parseFromString(token.BonusRewards); 
setA(xml)
var c= xml
console.log("anehemenet",xml)
console.log("a after mapjjjjj",a)
   //console.log("xml",a)
    //console.log("after cat")
    return xml;
  }
}

  return (
    <React.Fragment>
      <TableRow sx={{ '& > *': { borderBottom: 'unset' } }}>
        <TableCell align="right" component="th" scope="row" width="20%">
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => {
              console.log("onclick")
              setOpen(!open)
              if(openteamleader)
              setOpenteamleader(!openteamleader)
          
              }}
          >
            
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
           <PeopleIcon></PeopleIcon>
          </IconButton>
         
          


          <IconButton
            aria-label="expand row"
            size="small"
            onClick={handleSubmit}
          >
            {openteamleader ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
           <PersonIcon></PersonIcon>
          </IconButton>




        </TableCell>
        <TableCell align="right" component="th" scope="row">
          {row.attributes.TeamLeaderName}
        </TableCell>
      
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
           
          </Collapse>
          <Collapse in={openteamleader} timeout="auto" unmountOnExit>
           <GetRewardsofteamleader rewards={a.children}></GetRewardsofteamleader>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}


export default function RewardsTable(props) {
  return (
    <TableContainer component={Paper}>
      <Table aria-label="collapsible table">
        <TableHead>
          <TableRow>
            <TableCell />
            <TableCell align="right">ראש צוות</TableCell>
            
            
          </TableRow>
        </TableHead>
        <TableBody text-align="right">
          {props.token.teamleaders.map((row) => (
            <Row text-align="right" key={row.attributes.TeamLeader} row={row} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

 	 	 

